#projects {
  text-align: center;
  padding: 50px;
  color: white;
}

.projects-background {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom right, #b5ca19, #038d9f);
}

.card-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

@media (max-width: 792px) {
  #projects {
    padding: 30px;
  }

  .card-container {
    flex-direction: column;
    align-items: center;
  }
  .projects-background {
    position: inherit;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom right, #b5ca19, #038d9f);
  }
}
